:root {
  --bg-color: white;

  --size-micro: 0.125rem;
  --size-tiny: 0.25rem;
  --size-small: 0.5rem;
  --size-medium: 0.75rem;
  --size-medium-plus: 0.85rem;
  --size-large: 1rem;
  --size-largeish: 1.25rem;
  --size-larger: 1.75rem;
  --size-big: 2rem;
  --size-bigger: 2.5rem;
  --size-huge: 4rem;
  --size-epic: 8rem;

  --small-bp: 720px;
  --medium-bp: 1080px;
}

.App {
  text-align: center;
}

.App-header {
  background: var(--bg-color);
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  overflow: hidden;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.button {
  border: 1px solid #070808;
  color: #000000;
  padding: var(--size-small) var(--size-small);
  cursor: pointer;
  border-radius: var(--size-tiny);
  justify-content: center;
  min-width: 90px;
  font: inherit;
  text-decoration: unset;
  font-size: var(--size-medium);
}

@media (max-width: 720px) {
  .button {
    min-width: 70px;
  }
}

.button:hover {
  background-color: rgb(179, 187, 201);
}

.primary-button {
  background-color: #fff;
  box-shadow: 0 var(--size-micro) var(--size-tiny) rgba(0, 0, 0, .16);
}

.secondary-button {
  background-color: transparent;
}

.button-disabled {
  background-color: lightgrey;
  color: rgb(151, 148, 148);
  cursor: unset;
}

.button-disabled:hover {
  background-color: lightgrey;
}