.container {
    display: flex;
    flex-direction: column;
    margin: var(--size-epic) 0 0 var(--size-epic);
    width: 100%;
}

@media (max-width: 720px) {
    .container {
        margin-left: var(--size-small);
    }
}

.content-wrapper {
    display: flex;
    text-align: left;
    flex-flow: column;
    justify-content: space-between;
    z-index: 1;
    margin-left: var(--size-medium);
    max-width: 700px;
}

.header {
    font-size: var(--size-bigger);
    margin-bottom: var(--size-medium);
}

@media (max-width: 400px) {
    .header {
        font-size: var(--size-larger);
    }
}

.sub-content {
    font-size: var(--size-large);
    font-weight: normal;
    text-align: left;
}

.logo {
    height: 40vmin;
    pointer-events: none;
    filter: blur(5px);
}

@media (max-width: 720px) {
    .home-title {
        margin-top: var(--size-huge);
        margin-right: var(--size-large);
    }
}

.home-title span {
    position: relative;
    display: block;
    line-height: 1.2;
    border-radius: var(--size-small);
}

.home-title span::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: var(--bg-color);
    animation: a-ltr-after 4s cubic-bezier(.77, 0, .18, 1) forwards;
    transform: translateX(-101%);
    opacity: 0.75;
}

.home-title span::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: var(--bg-color);
    animation: a-ltr-before 4s cubic-bezier(.77, 0, .18, 1) forwards;
    transform: translateX(0);
}

.home-title span:nth-of-type(1)::before,
.home-title span:nth-of-type(1)::after {
    animation-delay: 2s;
}

.home-title span:nth-of-type(2)::before,
.home-title span:nth-of-type(2)::after {
    animation-delay: 2.5s;
}

@keyframes a-ltr-after {
    0% {
        transform: translateX(-100%)
    }

    100% {
        transform: translateX(101%)
    }
}

@keyframes a-ltr-before {
    0% {
        transform: translateX(0)
    }

    100% {
        transform: translateX(200%)
    }
}