.nav-container {
    display: flex;
    margin-right: 40px;
}

.nav-container-link {
    padding: 50px 20px 0 0;
    text-decoration: none;
    color: black;
    height: fit-content
}

.nav-container-link:hover {
    text-decoration: underline;
    color: rgb(217, 116, 116);
}

@media (max-width: 720px) {
    .nav-container {
        padding: 20px;
        width: 100%;
        background-color: black;
    }

    .nav-container-link {
        color: white;
        font-size: 30px;
    }
}